@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Merriweather', serif;
  background-color: #24292b;
  color: #f8fff8;
}

.movieImg{
  border-radius: 4px;
  box-shadow: -6px 6px 10px rgba(0, 0, 0, 0.7), 6px -6px 10px rgba(255, 255, 255, 0.2);
}

.linkImg{
  width: 64px;
  margin-top: 32px;
  margin-left: 16px;
}

footer{
  background-color: rgb(22, 22, 22);
  min-height: 128px;
  line-height: 32px;
}

a{
  text-decoration: none;
  color: #f8fff8;
}

.App-header {
  background: -webkit-linear-gradient(top, rgb(15, 134, 51) 0%, rgb(29, 92, 47) 50%);
  background: linear-gradient(180deg, rgb(15, 134, 51) 0%, rgb(29, 92, 47) 50%);
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  cursor: pointer;
  box-shadow: 0px 15px 8px rgba(0, 0, 0, 0.342);

}

.spinner {
  height: 80px;
  margin: auto;
}

.App-intro {
  font-size: large;
}

* {
  box-sizing: border-box;
}

.movies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.App-header h2 {
  margin: 0;
}

.add-movies {
  text-align: center;
}

.add-movies button {
  font-size: 16px;
  padding: 8px;
  margin: 0 10px 30px 10px;
}

.movie {
  padding: 5px 25px 10px 25px;
  max-width: 300px;
  min-width: 256px;
  margin-left: 32px;
  margin-top: 32px;
}

.errorMessage {
  margin: auto;
  font-weight: bold;
  margin-top: 50px;
  color: rgb(158, 32, 32);
}

.search {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 32px;
}

input[type="submit"] {
  padding: 5px;
  background-color:  rgb(29, 92, 47);
  color: rgb(246, 255, 246);
  border: 1px solid black;
  width: 80px;
  margin-left: 5px;
  cursor: pointer;
  border-radius: 6px;
  margin-left: 16px;
}

input[type="submit"]:hover {
  background-color: #e0f1df;
  color: rgb(34, 34, 34);
}

.search > input[type="text"]{
  width: 40%;
  min-width: 170px;
}

@media screen and (min-width: 694px) and (max-width: 915px) {
  .movie {
    max-width: 33%;
  }
}

@media screen and (min-width: 652px) and (max-width: 693px) {
  .movie {
    max-width: 50%;
  }
}

@media screen and (max-width: 651px) {
  .movie {
    max-width: 100%;
    margin: auto;
  }
}
